#fimp {
	position: fixed;
	display: none;
	top: 50%;
	right: 20px;
	transform: translate(-50%, -50%);
	margin: 0 auto;
	background: #EEE;
	border-radius: 6px;
	>span {
		position: absolute;
		background-color: red;
		color: white;
		height: 25px;
		width: 25px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: -15px;
		margin-left: -10px;
		border-radius: 50%;
		font-size: 0.9rem;
		font-weight: 700;
		cursor: pointer;
		z-index: 99;
	}
	button:disabled,
	button[disabled] {
		opacity: 0.3;
	}
	.loader,
	.loader:after {
		border-radius: 50%;
		width: 12px;
		height: 12px;
	}
	.loader {
		border-top: 3px solid rgba(255, 255, 255, 0.2);
		border-right: 3px solid rgba(255, 255, 255, 0.2);
		border-bottom: 3px solid rgba(255, 255, 255, 0.2);
		border-left: 3px solid #ffffff;
		-webkit-transform: translateZ(0);
		-ms-transform: translateZ(0);
		transform: translateZ(0);
		-webkit-animation: spin 1.1s infinite linear;
		animation: spin 1.1s infinite linear;
	}
}

.fimp{
	width: 36px;
	height: 36px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 0;
	margin-top: -1px;
	background: #F8F8F8;
	&:first-of-type{
		border-radius: 6px 6px 0 0;
	}
	&:last-of-type{
		border-radius: 0 0 6px 6px;
	}
	svg.icon{
		width:18px;
		height:18px;
	}
}

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}